import { inject } from '@angular/core';
import { ResolveFn, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { MatchingTenantApi } from '@core/data-access/apis/matching-tenant.api';
import { Observable, catchError, tap } from 'rxjs';

import { Tenant } from '@core/models';
import { HttpErrorResponse } from '@angular/common/http';
import { APP_ROUTE } from '@core/constants';
import { MatchingTenantsState } from '@shared/states/matching-tenants-state';

export const MyAdMatchingTenanListResolver: ResolveFn<any> = (
	route: ActivatedRouteSnapshot,
	state: RouterStateSnapshot,
	router: Router = inject(Router),
	matchingTenantApi: MatchingTenantApi = inject(MatchingTenantApi),
	myAdTenantState: MatchingTenantsState = inject(MatchingTenantsState)
): Observable<any> => {
	return matchingTenantApi.getMatchingTenantList(route.params['adId']).pipe(
		catchError((error: HttpErrorResponse) => {
			router.navigate([APP_ROUTE.wildCard], { skipLocationChange: true });

			throw new Error(error.message);
		}),
		tap((tenantList: Tenant[]) => myAdTenantState.setMatchingTenantsList(tenantList))
	);
};
