import { Component, OnInit } from '@angular/core';
import { APP_ROUTE } from '@core/constants';
import { Tenant } from '@core/models';
import { Observable, catchError } from 'rxjs';

import { ActivatedRoute, Params, Router } from '@angular/router';
import { CommonFacade } from '@core/facades/common.facade';
import { MatchingTenantsFacade } from '@shared/facades/matching-tenants-facade';
import { Location } from '@angular/common';

@Component({
    selector: 'findqo-my-ad-matching-tenant-list',
    templateUrl: './my-ad-matching-tenant-list.page.html',
    styleUrls: ['./my-ad-matching-tenant-list.page.scss'],
    standalone: false
})
export class MyAdMatchingTenantListPage implements OnInit {
	constructor(
		private myAdMatchingTenantstFacade: MatchingTenantsFacade,
		private route: ActivatedRoute,
		private commonFacade: CommonFacade,
		private readonly router: Router
	) {
		this.matchingTenantList$ = this.myAdMatchingTenantstFacade.matchingTenantsList$;
	}
	matchingTenantList$: Observable<Tenant[]>;
	readonly appRoute = APP_ROUTE;
	adId: string;
	sortContactedList = false;
	isDisableSchedule = false;

	handleBack(): void {
		const URL = this.router.url.includes('dashboard') ? APP_ROUTE.agentHub.dashboard : APP_ROUTE.myAdList;
		this.router.navigate([URL]);
	}

	onScheduleTenantView(tenantData: Tenant): void {
		this.isDisableSchedule = true;
		this.commonFacade.loadAvailiblitySlots();

		this.myAdMatchingTenantstFacade
			.getMatchingTenantDetail(this.adId, tenantData?.id)
			.pipe(
				catchError((error) => {
					this.isDisableSchedule = false;
					throw new Error(error);
				})
			)
			.subscribe((res: { data: Tenant }) => {
				this.isDisableSchedule = false;
				this.myAdMatchingTenantstFacade.openTenantViewDateDialog(res?.data, this.adId);
			});
	}

	private getAdId(): void {
		this.route.params.subscribe((params: Params) => {
			this.adId = params['adId'];
		});
	}

	onSlideToggle(): void {
		this.sortContactedList = !this.sortContactedList;
	}

	ngOnInit(): void {
		this.getAdId();
	}
}
